@media only screen and (max-width: 960px) {
  #header {
    height: 80vh;
    text-align: center;
  }
  #header .header_img {
    text-align: center;
  }
  #header .header_img img {
    width: 60%;
  }
  .title-logo {
    width: 170.66px;
    height: 40px;
  }
  .service-box {
    max-width: 45%;
  }
}
@media only screen and (max-width: 768px) {
  #header {
    height: auto;
    margin-top: 20px;
  }
  #header h2 {
    font-size: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header .header_img img {
    width: 90%;
  }
  .animated {
    height: 150px;
  }
  .form-floating {
    width: 100% !important;
  }
  .title-logo {
    width: 170.66px;
    height: 40px;
  }
  .service-box {
    max-width: 50%;
  }
  .intro-imgdiv {
    width: 100%;
  }
  .intro-textdiv {
    width: 100%;
  }
  .intro-main{
    height:180vh ;
  }
  .intro-div{
    height: 45vh;
  }
  .contact-2h{
    padding-left: 0px;
    padding: auto;
}
.contact-div-1,.contact-div-2{
  float: none ;
  width: 100%;
  padding-left: 0px;
  text-align: left;
padding-left: 20px;
}

}
@media screen and (max-width: 480px) {
  .service-box {
    max-width: 100%;
  }
  .intro-main{
    height:220vh ;
  }
  .intro-div{
    height: 55vh;
  }
  .contact-2h{
    padding-left: 0px;
    
}.contact-2h h4{
font-size: 18px;
}
.contact-div-1,.contact-div-2{
  float: none ;
  width: 100%;
  padding-left: 0px;
  text-align: left;
padding-left: 20px;
}

}
